<script setup>
import { ref, watch, onMounted } from 'vue';
import { useRoute } from 'vue-router';
import * as yup from 'yup';
import { useReCaptcha } from 'vue-recaptcha-v3';
import { useForm, useField } from 'vee-validate';
import getHost from '@/utilities/GetHost';
import Footer from '@/components/core/Footer.vue';

const route = useRoute();

const { executeRecaptcha, recaptchaLoaded } = useReCaptcha();

const EMAIL_REGX = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;

const types = ref(['Accommodation', 'Group / Event', 'Other']);
const reducedMotion = ref(false);
const sent = ref(false);

const honeypot = ref();
const honeypotTimer = ref(false);

const schema = yup.object().shape({
  firstname: yup.string().required('First Name is required'),
  lastname: yup.string().required('Last Name is required'),
  email: yup.string()
    .required('Email Address is required')
    .matches(EMAIL_REGX, 'Invalid email address'),
  phone: yup.string().required('Phone Number is required'),
  company: yup.string().optional(),
  type: yup.string()
    .oneOf(types.value, 'Invalid enquiry type')
    .required('Enquiry Type is required'),
  date: yup.date()
    .when('type', {
      is: 'Other',
      then: (d) => d.optional(),
      otherwise: (d) => d.typeError('Invalid date').required('Date is required')
    }),
  guests: yup.number()
    .when('type', {
      is: 'Other',
      then: (d) => d.optional(),
      otherwise: (d) => d.required('Guest Count is required')
        .positive('Guest Count must be above 0')
        .integer('Guest Count must be a whole number')
    }),
  message: yup.string()
    .required('Message is required')
    .min(20, 'Must be at least 20 characters long')
    .max(2000, 'Must be 2000 characters long or less')
});

const { handleSubmit, resetForm } = useForm({
  validationSchema: schema
});

const { value: firstname, errorMessage: firstnameError } = useField('firstname');
const { value: lastname, errorMessage: lastnameError } = useField('lastname');
const { value: email, errorMessage: emailError } = useField('email');
const { value: phone, errorMessage: phoneError } = useField('phone');
const { value: company, errorMessage: companyError } = useField('company');
const { value: type, errorMessage: typeError } = useField('type');
const { value: date, errorMessage: dateError } = useField('date');
const { value: guests, errorMessage: guestsError } = useField('guests');
const { value: message, errorMessage: messageError } = useField('message');

function closeSent(e) {
  e.stopPropagation();

  sent.value = false;
}

async function recaptcha() {
  await recaptchaLoaded();

  const token = await executeRecaptcha('contact');

  return token;
}

const onSubmit = handleSubmit((values) => {
  recaptcha().then((token) => {
    if (!honeypotTimer.value) {
      // Likely a bot, filled form too quickly, directly show success after random timeout without processing data
      alert('Too quick');
    } else if (honeypot.value) {
      // Likely a bot, honeypot was filled in, directly show success after random timeout without processing data
      alert('Honeypot filled in');
    } else {
      values.recaptcha = token;
      // fetch(`${getHost()}/bookings/contact`, { // TODO: Uncomment after demonstration build
      //   method: 'POST',
      //   headers: {
      //     'Content-Type': 'application/json',
      //   },
      //   body: JSON.stringify(values)
      // });
      // .then((res) => res.json()).then((result) => {
      //   // eslint-disable-next-line no-alert
      //   alert(JSON.stringify(result));
      // });
      alert(JSON.stringify(values));
    }

    sent.value = true;
    resetForm();
  });
});

watch(() => route.path, (newp, oldp) => {
  if (oldp !== newp) {
    document.getElementsByClassName('contact-scroll')[0].scrollTo({ top: 0, behavior: reducedMotion.value ? 'auto' : 'smooth' });
  }
});

onMounted(() => {
  reducedMotion.value = window.matchMedia('(prefers-reduced-motion: reduce)').matches;

  setTimeout(() => {
    honeypotTimer.value = true;
    document.querySelector('#terms').tabIndex = -1;
    document.querySelector('#terms').autocomplete = 'off';
  }, 5 * 1000);
});
</script>

